	module.exports = {"data":{"markdownRemark":{"html":"<img align=\"left\" alt=\"screenshot chinese yahoo\" src=\"/static/images/yahoo_unfiltered_search_before_2.jpg\" />\n<p>Recently, the mega search engine Yahoo! collaborated with Chinese authorities in <a href=\"http://www.rsf.org/article.php3?id_article=14884\">jailing Chinese journalist Shi Tao</a> for allegedly distributing state secrets. Apparently Yahoo has taken the initiative to provide the journalist's email address and IP address in order to link him to posts and emails that he was making while distributing information. Why is Yahoo participating in the Chinese censorship program?</p>\n<p>Because it's good business. Yesterday <a href=\"http://rconversation.blogs.com/\">Rebecca MacKinnon </a>took the time to illustrate exactly what it's like to search for \"Tiananmen Massacre\" on the Chinese Yahoo.</p>","frontmatter":{"created_at":"September 19, 2005","path":"/2005/09/19/yahoo-patricipates-in-chinese-censorship","title":"Yahoo Participates in Chinese Censorship","tags":["yahoo","censorship","freespeech","china"]}}},"pathContext":{}}

